import {Component,ViewEncapsulation} from '@angular/core';
import {Employee} from './employees/employee';
import {Report} from './employees/employee';
import {EmployeeService} from './employees/employeeservice';

class PrimeEmployee implements Employee {
    constructor(public SRNo?, public AppointmentDate?, public DistrictName?, public TruckSerialNo?, public Equipment?, public Status?, public Address?,
    public RequestedTotal?, public PickedUpTotal?, public RequestedTVs?,public PickedUpTVs?,public RequestedOther?, public PickedUpOther?) {}
}

class PrimeReport implements Report {
    constructor(public SumRequestedTotal?, public SumPickedUpTotal?, public SumRequestedTVs?, public SumPickedUpTVs?, public SumRequestedOther?, public SumPickedUpOther?,public ReportItems?) {}
}

@Component({
	templateUrl: './app.component.html',
	selector: 'my-app',
  styleUrls: ['./app.component.css'],
  encapsulation: ViewEncapsulation.None
})
export class AppComponent {

    objSrNo: string;
    objAddress: string;
    objFromDate: string;
    objToDate: string;
    objDistrict: string;
    objEquipment: string;
    objStatus: string;

    ddlDistricts: string[];
    distNum:number;
    ddlEquipments: string[];
    equNum:number;
    ddlStatuss: string[];
    statNUm:number;

	displayDialog: boolean;

    employee: Employee = new PrimeEmployee();

    selectedEmployee: Employee;

    newEmployee: boolean;

    employees: Employee[];

    sumRequestedTotal:string;
    sumPickedUpTotal:string;
    sumRequestedTVs:string;
    sumPickedUpTVs:string;
    sumRequestedOther:string;
    sumPickedUpOther:string;

    // report: Report = new PrimeReport();

    constructor(private employeeService: EmployeeService) { }

    ngOnInit() {
        this.employeeService.getEmployeesMedium(this.objSrNo,this.objAddress,this.objFromDate,this.objToDate,this.objDistrict,this.objEquipment,this.objStatus)
        .then(abc => {this.employees = abc.ReportItems;
            this.sumRequestedTotal=abc.SumRequestedTotal;
            this.sumPickedUpTotal=abc.SumPickedUpTotal;
            this.sumRequestedTVs=abc.SumRequestedTVs;
            this.sumPickedUpTVs=abc.SumPickedUpTVs;
            this.sumRequestedOther=abc.SumRequestedOther;
            this.sumPickedUpOther=abc.SumPickedUpOther;

            var unique = this.employees.map(function(a) {return a.DistrictName;});
             this.ddlDistricts= unique.filter( this.onlyUnique );
             this.ddlDistricts.unshift(' ');
            
             unique = this.employees.map(function(a) {return a.TruckSerialNo;});
             this.ddlEquipments= unique.filter( this.onlyUnique );
             this.ddlEquipments.unshift(' ');
            
             unique = this.employees.map(function(a) {return a.Status;});
             this.ddlStatuss= unique.filter( this.onlyUnique );
             this.ddlStatuss.unshift(' ');
        });
    }
	
    getReport()
    {
        this.employeeService.getEmployeesMedium(this.objSrNo,this.objAddress,this.objFromDate,this.objToDate,this.objDistrict,this.objEquipment,this.objStatus)
        .then(abc => {this.employees = abc.ReportItems;
            this.sumRequestedTotal=abc.SumRequestedTotal;
            this.sumPickedUpTotal=abc.SumPickedUpTotal;
            this.sumRequestedTVs=abc.SumRequestedTVs;
            this.sumPickedUpTVs=abc.SumPickedUpTVs;
            this.sumRequestedOther=abc.SumRequestedOther;
            this.sumPickedUpOther=abc.SumPickedUpOther;
        });
    }

    setDist(value){
        this.objDistrict = value.split(' ')[1].trim();
        this.getReport();
    }

    setEqu(value){
        this.objEquipment = value.split(' ')[1].trim();
        this.getReport();
    }

    setStat(value){
        this.objStatus = value.substr(value.indexOf(' ')+1);         
        this.getReport();
    }

    onlyUnique(value, index, self) { 
        return self.indexOf(value) === index;
    }
}


